a {text-decoration:none; color:#222;}

.navigation{
  display: flex;
  flex-direction: column;
  position: absolute;
  left:10px;
  top:10px;
  background-color: #fff;
  font-size: 15px;
  font-weight: 500;
  padding: 10px;
  box-shadow:0 13px 27px -5px rgba(50, 50, 93, 0.25),
  0 8px 16px -8px rgba(0,0,0, 0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);
}

.navigation a{
  text-decoration: none;
  margin: 10px;
}