.aboutwrap{
  display: block;
  width: 100%;
  align-items: center;
}

.about{
  display: block;
  max-width: 700px;
  background-color: #fff;
  box-shadow:0 13px 27px -5px rgba(50, 50, 93, 0.25),
  0 8px 16px -8px rgba(0,0,0, 0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);
  margin: 50px;
  padding: 20px;
  border-radius: 30px;
  font-size: 12px;
  font-weight: bold;
}